import React, { useState } from 'react';
import Button from 'devextreme-react/button';
import TagBox from 'devextreme-react/tag-box';
import SelectBox from 'devextreme-react/select-box';
import { UserInfo } from './data';

const REGIONS = [
    'Erebuni',
    'Kentron',
    'Arabkir',
    'Avan',
    'Shirak',
    'Nork-Marash',
    'Davtashen',
    'Shengavit',
    'Achapnyak',
].sort();

const LANGS = [
    'ru',
    'am',
    'en',
].sort();

const TYPES = [ 'water', 'electricity' ];

interface UserEditFormProps {
    userInfo: UserInfo;
    onSave: (updatedUserInfo: UserInfo) => void;
}

const UserEditForm: React.FC<UserEditFormProps> = ({ userInfo, onSave }) => {
    const [ regions, setRegions ] = useState(userInfo.regions);
    const [ lang, setLang ] = useState(userInfo.lang);
    const [ types, setTypes ] = useState(userInfo.types);


    return <div>
        <SelectBox label={'Lang'} items={LANGS} value={lang} onValueChanged={(e) => setLang(e.value)}></SelectBox>
        <TagBox label={'Sources'} items={TYPES} value={types} onValueChanged={e => setTypes(e.value)}></TagBox>
        <TagBox label={'Regions'} items={REGIONS} value={regions} onValueChanged={e => setRegions(e.value)}
                acceptCustomValue={true}></TagBox>
        <Button
            text="Save"
            onClick={() => onSave({ types, lang, regions })}
        />
    </div>;
};

export default UserEditForm;

